import { Button, Dropdown, TextInput } from 'ether-ui';
import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { TemplateComponent } from '../../../types/template';
import { LoadingSpinner } from '../../components/LoadingSpinner';
import { useIntegrationStore } from '../../services/integration';
import { useTemplateStore } from '../../services/template';
import { TemplatePreview } from './TemplatePreview';

export const TemplateForm: React.FC = () => {
    const navigate = useNavigate();
    const { id } = useParams<{ id: string }>();
    const isEditMode = !!id;

    const { createTemplate, updateTemplate, getTemplateById, selectedTemplate, isLoading } = useTemplateStore();
    const { loadIntegrations, integrations } = useIntegrationStore();

    const [formData, setFormData] = useState({
        integrationId: '',
        type: 'gupshup' as any,
        name: '',
        displayName: '',
        category: 'MARKETING' as any,
        language: 'en',
        components: [] as TemplateComponent[],
    });

    const [currentComponent, setCurrentComponent] = useState<Partial<TemplateComponent>>({
        type: 'BODY',
        text: '',
    });

    useEffect(() => {
        loadIntegrations();
        if (isEditMode && id) {
            getTemplateById(id);
        }
    }, [id]);

    useEffect(() => {
        if (isEditMode && selectedTemplate) {
            setFormData({
                integrationId: selectedTemplate.integrationId,
                type: selectedTemplate.type,
                name: selectedTemplate.name,
                displayName: selectedTemplate.displayName,
                category: selectedTemplate.category || 'MARKETING',
                language: selectedTemplate.language,
                components: selectedTemplate.components,
            });
        }
    }, [selectedTemplate, isEditMode]);

    const categoryOptions = [
        { label: 'Marketing', value: 'MARKETING' },
        { label: 'Utility', value: 'UTILITY' },
        { label: 'Authentication', value: 'AUTHENTICATION' },
    ];

    const componentTypeOptions = [
        { label: 'Header', value: 'HEADER' },
        { label: 'Body', value: 'BODY' },
        { label: 'Footer', value: 'FOOTER' },
        { label: 'Buttons', value: 'BUTTONS' },
    ];

    const languageOptions = [
        { label: 'English', value: 'en' },
        { label: 'Spanish', value: 'es' },
        { label: 'French', value: 'fr' },
        { label: 'German', value: 'de' },
        { label: 'Hindi', value: 'hi' },
    ];

    const integrationOptions = integrations.filter((int) => int.isActive).map((int) => ({ label: int.name, value: int.id }));

    const addComponent = () => {
        if (currentComponent.type && currentComponent.text) {
            setFormData({
                ...formData,
                components: [...formData.components, currentComponent as TemplateComponent],
            });
            setCurrentComponent({ type: 'BODY', text: '' });
        }
    };

    const removeComponent = (index: number) => {
        setFormData({
            ...formData,
            components: formData.components.filter((_, i) => i !== index),
        });
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        try {
            if (isEditMode && id) {
                await updateTemplate(id, {
                    displayName: formData.displayName,
                    category: formData.category,
                    components: formData.components,
                });
            } else {
                await createTemplate(formData);
            }
            navigate('/templates');
        } catch (error) {
            console.error('Failed to save template:', error);
        }
    };

    if (isEditMode && !selectedTemplate && isLoading) {
        return (
            <div className="flex items-center justify-center py-12">
                <LoadingSpinner />
            </div>
        );
    }

    if (isEditMode && !selectedTemplate) {
        return (
            <div className="text-center py-12">
                <p className="text-gray-500">Template not found</p>
            </div>
        );
    }

    return (
        <div className="max-w-6xl mx-auto space-y-6">
            <div className="flex items-center justify-between">
                <div>
                    <h2 className="text-2xl font-bold text-gray-900">{isEditMode ? 'Edit Template' : 'Create Template'}</h2>
                    <p className="text-gray-600 mt-1">{isEditMode ? formData.name : 'Build a new message template'}</p>
                </div>
                <Button variant="default" onClick={() => navigate('/templates')}>
                    Cancel
                </Button>
            </div>

            <form onSubmit={handleSubmit}>
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                    <div className="space-y-6">
                        <div className="bg-white rounded-lg border border-gray-200 p-6 space-y-4">
                            <h3 className="font-semibold text-gray-900">Template Information</h3>

                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-2">Integration *</label>
                                <Dropdown
                                    options={integrationOptions}
                                    value={formData.integrationId}
                                    onChange={(e) => setFormData({ ...formData, integrationId: e.value })}
                                    placeholder="Select integration"
                                    disabled={isEditMode}
                                />
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-2">Template Name *</label>
                                <TextInput
                                    value={formData.name}
                                    onChange={(e) => setFormData({ ...formData, name: e.value })}
                                    placeholder="e.g., welcome_new_user"
                                    disabled={isEditMode}
                                />
                                <p className="text-xs text-gray-500 mt-1">Use lowercase, numbers, and underscores only</p>
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-2">Display Name *</label>
                                <TextInput
                                    value={formData.displayName}
                                    onChange={(e) => setFormData({ ...formData, displayName: e.value })}
                                    placeholder="Welcome New User"
                                />
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-2">Category *</label>
                                <Dropdown
                                    options={categoryOptions}
                                    value={formData.category}
                                    onChange={(e) => setFormData({ ...formData, category: e.value as any })}
                                />
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-2">Language *</label>
                                <Dropdown
                                    options={languageOptions}
                                    value={formData.language}
                                    onChange={(e) => setFormData({ ...formData, language: e.value })}
                                    disabled={isEditMode}
                                />
                            </div>

                            {isEditMode && (
                                <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
                                    <p className="text-sm text-yellow-800">
                                        Note: Template name, language, and integration cannot be changed after creation.
                                    </p>
                                </div>
                            )}
                        </div>

                        {!isEditMode && (
                            <div className="bg-white rounded-lg border border-gray-200 p-6 space-y-4">
                                <h3 className="font-semibold text-gray-900">Add Components</h3>

                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">Component Type</label>
                                    <Dropdown
                                        options={componentTypeOptions}
                                        value={currentComponent.type || ''}
                                        onChange={(e) => setCurrentComponent({ ...currentComponent, type: e.value as any })}
                                        placeholder="Select type"
                                    />
                                </div>

                                {currentComponent.type && currentComponent.type !== 'BUTTONS' && (
                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-2">Text</label>
                                        <textarea
                                            rows={3}
                                            value={currentComponent.text || ''}
                                            onChange={(e) => setCurrentComponent({ ...currentComponent, text: e.target.value })}
                                            placeholder="Enter text... Use {{1}}, {{2}} for variables"
                                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                                        />
                                        <p className="text-xs text-gray-500 mt-1">Use {`{{1}}, {{2}}`} for dynamic variables</p>
                                    </div>
                                )}

                                <Button type="button" onClick={addComponent} disabled={!currentComponent.type || !currentComponent.text}>
                                    Add Component
                                </Button>

                                {formData.components.length > 0 && (
                                    <div className="space-y-2 mt-4">
                                        <h4 className="text-sm font-medium text-gray-700">Added Components</h4>
                                        {formData.components.map((component, index) => (
                                            <div
                                                key={index}
                                                className="flex items-center justify-between bg-gray-50 border border-gray-200 rounded-lg p-3"
                                            >
                                                <div>
                                                    <span className="font-medium text-sm">{component.type}</span>
                                                    {component.text && (
                                                        <p className="text-xs text-gray-600 mt-1 truncate max-w-xs">{component.text}</p>
                                                    )}
                                                </div>
                                                <Button variant="default" size="sm" type="button" onClick={() => removeComponent(index)}>
                                                    Remove
                                                </Button>
                                            </div>
                                        ))}
                                    </div>
                                )}
                            </div>
                        )}
                    </div>

                    <div className="space-y-6">
                        <div className="bg-white rounded-lg border border-gray-200 p-6">
                            <h3 className="font-semibold text-gray-900 mb-4">Preview</h3>
                            {formData.components.length > 0 ? (
                                <TemplatePreview template={formData as any} />
                            ) : (
                                <div className="text-center py-8 text-gray-500">Add components to see preview</div>
                            )}
                        </div>
                    </div>
                </div>

                <div className="flex justify-end space-x-3 mt-6">
                    <Button type="button" variant="default" onClick={() => navigate('/templates')}>
                        Cancel
                    </Button>
                    <Button
                        type="submit"
                        disabled={isLoading || !formData.integrationId || !formData.name || formData.components.length === 0}
                    >
                        {isLoading ? (isEditMode ? 'Updating...' : 'Creating...') : isEditMode ? 'Update Template' : 'Create Template'}
                    </Button>
                </div>
            </form>
        </div>
    );
};

export default TemplateForm;
