import React from 'react';
import { Template } from '../../../types/template';

interface TemplatePreviewProps {
    template: Template;
}

export const TemplatePreview: React.FC<TemplatePreviewProps> = ({ template }) => {
    return (
        <div className="grid grid-cols-2 gap-6">
            <div>
                <h4 className="font-medium text-gray-900 mb-3">Message Preview</h4>
                <div className="bg-white border rounded-lg p-4 max-w-sm">
                    {template.components?.map((component, idx) => (
                        <div key={idx} className="mb-3 last:mb-0">
                            {component.type === 'HEADER' && (
                                <div className="font-semibold text-gray-900 text-sm mb-2">{component.text}</div>
                            )}
                            {component.type === 'BODY' && (
                                <div className="text-gray-800 text-sm mb-2 whitespace-pre-wrap">{component.text}</div>
                            )}
                            {component.type === 'FOOTER' && <div className="text-xs text-gray-600 mb-2">{component.text}</div>}
                            {component.type === 'BUTTONS' && component.buttons && (
                                <div className="space-y-1">
                                    {component.buttons.map((button, btnIdx) => (
                                        <div
                                            key={btnIdx}
                                            className="bg-blue-50 text-blue-700 px-3 py-1 rounded text-xs border border-blue-200 text-center"
                                        >
                                            {button.text}
                                        </div>
                                    ))}
                                </div>
                            )}
                        </div>
                    ))}
                </div>
            </div>

            <div>
                <h4 className="font-medium text-gray-900 mb-3">JSON Structure</h4>
                <div className="bg-gray-900 text-gray-100 rounded-lg p-4 text-xs font-mono overflow-x-auto max-h-96 overflow-y-auto">
                    <pre>
                        {JSON.stringify(
                            {
                                name: template.name,
                                language: template.language,
                                category: template.category,
                                components: template.components,
                                metadata: template.metadata,
                            },
                            null,
                            2
                        )}
                    </pre>
                </div>
            </div>
        </div>
    );
};
